<template>
	<div>
		<div class="kd-page-title">输入驾驶证证心编号</div>
		<div class="kd-page-content-wrapper wrap-info">
			<kd-full-input
					:ime-type="3"
					ref="carInput"
					placeholder="请输入驾驶证证心编号"
					:watch-data="getCarChip"
					:get-focus="getFocus"
					:limit-number="13"
					class="chipInput"
			/>
			<img alt="" class="imgWrap" src="../../assets/yinchuan/driverChip.png">
		</div>
		<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
			<kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
			<kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="nextStep" :but-disable="!chip"/>
		</div>
	</div>
</template>

<script>
import { mapMutations } from 'vuex';

export default {
    name: 'input-car-chip',
    data () {
        return {
            chip: '',
            inputVueEl: null
        };
    },
    props: {
        getCarInput: { type: Function },
        nextFunction: {
            type: Function,
            default: function () {
                this.changeStep('down');
            }
        }
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        async nextStep () {
            await this.nextFunction(this.chip);
        },
        getFocus (data) {
            this.inputVueEl = data;
        },
        getCarChip (data) {
            this.chip = data;
        }
    }

};
</script>

<style lang="scss" scoped>
	.wrap-info {
		display: flex;
		justify-content: center;
		align-content: center;
		flex-direction: column;
	}

	.imgWrap {
		margin: 20PX auto;
		width: 500PX;
	}

	.chipInput {
		border-radius: 6px;
		font-size: 18px;
		color: rgba(142, 156, 185, 1);
		line-height: 37px;
	}
</style>
