<template>
	<div>
		<server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
		<identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')" :generateCard="true"/>
		<face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
		<contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
		<process :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'process')" :handle-business="handleBusiness"/>
		<input-driver-chip :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'inputDriverChip')" :nextFunction="getDriverChip"/>
		<textAppear v-if="currentStep === steps.findIndex(step=>step.key === 'driverInfo')" :formListData="driverLicenseInfo" :nextFunction="nextSureInfo"></textAppear>
		<photo-tip-second v-if="currentStep === steps.findIndex(step=>step.key === 'photoTip')" :nextFunction="photoNext" :photoNumber="3"/>

		<process :type="6" v-if="currentStep === steps.findIndex(step=>step.key === 'driverPhotoHandle')" :handle-business="driverPhotoHandle" waitText="正在验证照片，请稍后......" goToRoute="photoTip"/>

		<high-photo v-if="currentStep === steps.findIndex(step=>step.key === 'highPhoto')" itemName="体检报告" :media="media" :nextFunction="highPhotoNext"/>
		<mail-type v-if="currentStep === steps.findIndex(step=>step.key === 'mailType')" :nextFunction="mailAddressUpload" :receivingAddress="receivingAddress" :mailTip="mailTip"/>
		<process :type="5" v-if="currentStep === steps.findIndex(step=>step.key === 'processPhoto')" :handle-business="uploadDriverInfo"/>
		<unified-payment-choose v-if="currentStep === steps.findIndex(step=>step.key === 'payChoose')" :payQrCode="payQrCode" :nextFunction="getUnifiedPayment"/>
		<unified-payment-appear v-if="currentStep === steps.findIndex(step=>step.key === 'payResult')" :payQrCode="payQrCode" :nextFunction="checkUnifiedPayment"/>
		<!--        <anhui-pay-qr-->
		<!--            v-if="currentStep === steps.findIndex(step=>step.key === 'pay')"-->
		<!--            :payInfo="lsh"-->
		<!--            :payCode="payQrCode"-->
		<!--            :checkPayStatus="checkPayStatus"-->
		<!--            :disableNextButton="true"-->
		<!--        />-->
		<process :type="2" v-if="currentStep === steps.findIndex(step=>step.key === 'completeBusiness')" :handle-business="driverChangeGetPaperPath"/>
		<photo-preview v-if="currentStep === steps.findIndex(step=>step.key === 'photoPrint')" :previewPrintPhoto="handlePic" :nextFunction="photoPrintNext" :disableTake="true"/>
		<process :type="3" v-if="currentStep === steps.findIndex(step=>step.key === 'tickPrint')" :handle-business="tickPrint" waitText="正在打印业务凭条，请稍后......"/>
		<process :type="4" v-if="currentStep === steps.findIndex(step=>step.key === 'completeProcess')" :handle-business="getResultSerialNumber"/>
		<complete v-if="currentStep === steps.findIndex(step=>step.key === 'complete')" :data="completeInfo" footTexts="温馨提示：请取走业务申请表或凭条"/>
	</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import serverInfo from '@/components/server-info';
import complete from '@/components/complete';
import process from '@/components/process';
import textAppear from '@/view/pageComponents/text-appear';
import photoTipSecond from '@/view/pageComponents/photo-tip-second';
import highPhoto from '@/view/pageComponents/high-photo';
import inputDriverChip from '@/view/pageComponents/input-driver-chip';
import unifiedPaymentChoose from '@/view/pageComponents/unified-payment-choose';
import unifiedPaymentAppear from '@/view/pageComponents/unified-payment-appear';
import photoPreview from '@/view/pageComponents/photo-preview';
import mailType from '@/view/pageComponents/mail-type';
import { DRIVER_LICENSE_LOST_RENEWAL, DRIVER_LICENSE_RENEWAL } from '@/constant/stepsOption';
import { getCertificates, checkIsSatisfy, saveDriverPhoto, uploadPhysicalInfo, completeUpload, driverGetSerialNumber, getPhysicalInfo, driverChangeGetPaperPath } from '@/service/traffic/driver/replacement';
import { REPLACEMENT_TYPES } from '@/constant/constant';
import { getDriverLicensePhoto, handleDriverPhotoRequest } from '@/utils/axiosHandle';
import { cancelPrefix } from '@/utils/tools';
import media from '@/assets/media/trafic/22.mp3';
import { excelPrint, printPhotoBase } from '@/service/common';
import request from '@/utils/request';
import { checkPayIncomePayment, getPayIncomePayment } from '@/service/traffic/traffic';
import { getIdentificationPhoto } from '@/service/convenient/identificationPhoto';
import { vehLicenseSaveMailInfo } from '@/service/traffic/vehicle/licenseRenewal';
import { mailTipMessage } from '@/utils/regional';

export default {
    name: 'DriverExchangeEvidenceIndex',
    components: { identifyCardPage, faceYitu, contrast, serverInfo, complete, process, textAppear, photoTipSecond, unifiedPaymentChoose, unifiedPaymentAppear, photoPreview, highPhoto, mailType, inputDriverChip },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            businessTypeName: 'businessTypeName',
            binocularCamera: 'binocularCamera',
            identifyCard: 'identifyCard',
            identifyCardPicture: 'identifyCardPicture'
        }),
        ...mapState('permanent', {
            originBusinessObject: 'originBusinessObject',
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        })
    },
    data () {
        return {
            chip: '',
            mailTip: '',
            receivingAddress: '领取地址：<br/>请确保现场有驾驶证制证自助终端，<br/>可选择现场制证',
            media: media,
            lsh: '',
            payQrCode: null,
            driverLicenseInfo: {},
            paperPath: '',
            handlePic: '', // 未修改成一寸之前照片
            photoDogHandlePic: '' // 修改一寸照片之后
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage', 'setUserRecognitionId', 'setUserBusinessId', 'setCurrentBusinessName', 'setCurrentBusinessHandle']),
        async handleBusiness () {
            const res = await checkIsSatisfy({
                identifiedPhoto: this.binocularCamera,
                type: REPLACEMENT_TYPES[this.businessTypeCode]
            });
            if (res && res.code === '0') {
                await this.getCertificates();
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async getCertificates () {
            const res = await getCertificates({
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                this.driverLicenseInfo = res.result;
                if (res.result.skipStatus === 1) { // slh获得，pincdoe未成功
                    this.handleRepeatBusiness(res);
                    this.changeStep('payChoose');
                } else if (res.result.skipStatus === 2) { // pincode成功，支付未成功
                    this.payQrCode = res.result.pinCode;
                    this.handleRepeatBusiness(res);
                    this.changeStep('payResult');
                } else if (res.result.skipStatus === 3) { // 支付成功，未办结
                    this.payQrCode = res.result.pinCode;
                    this.handleRepeatBusiness(res);
                    this.changeStep('completeBusiness');
                } else if (res.status === -100) {
                    this.setCurrentBusinessHandle(this.BUSINESS_EN_NAME_CODE.driverExpiration);
                    this.setCurrentBusinessName(this.originBusinessObject.find((i) => {
                        return i.businessCode === this.BUSINESS_EN_NAME_CODE.driverExpiration;
                    }).businessName);
                    this.changeDriverSteps();
                    this.changeStep('down');
                } else {
                    this.changeStep('down');
                }
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        handleRepeatBusiness (res) {
            this.lsh = res.result.lsh;
            this.handlePic = res.result.photo;
            this.setUserRecognitionId(res.result.userRecognitionId);
            this.setUserBusinessId(res.result.businessId);
            this.setCurrentBusinessHandle(res.result.businessTypeCode);
            this.setCurrentBusinessName(this.originBusinessObject.find((i) => {
                return i.businessCode === res.result.businessTypeCode;
            }).businessName);
            this.changeDriverSteps();
        },
        changeDriverSteps () {
            if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.driverExpiration) {
                const matchItem = this.steps.find((i) => {
                    return i.key === 'highPhoto';
                });
                if (!matchItem) {
                    this.setSteps(DRIVER_LICENSE_RENEWAL);
                }
            } else {
                const matchItem = this.steps.find((i) => {
                    return i.key === 'highPhoto';
                });
                if (matchItem) {
                    this.setSteps(DRIVER_LICENSE_LOST_RENEWAL);
                }
            }
        },
        getDriverChip (chip) {
            this.chip = chip;
            this.changeStep('down');
        },
        async nextSureInfo () {
            if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.driverExpiration) {
                const res = await getPhysicalInfo({
                    type: REPLACEMENT_TYPES[this.businessTypeCode]
                });
                if (res && res.code === '0') {
                    this.changeStep('down');
                } else if (res) {
                    this.changeTipPage({ message: res.message, enableBack: true });
                }
            } else {
                this.changeStep('down');
            }
        },
        async uploadDriverInfo () {
            const res = await completeUpload();
            if (res && res.code === '0') {
                this.lsh = res.result.lsh;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async getUnifiedPayment () {
            const res = await getPayIncomePayment({ lsh: this.lsh });
            if (res && res.code === '0') {
                this.payQrCode = res.result.pinCode;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async checkUnifiedPayment () {
            const res = await checkPayIncomePayment({ lsh: this.lsh });
            if (res && res.code === '0') {
                this.changeStep('down');
            }
        },
        async driverChangeGetPaperPath () {
            const res = await driverChangeGetPaperPath();
            if (res && res.code === '0') {
                this.paperPath = res.result.paperPath;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async photoNext (photoBase) {
            const res = await handleDriverPhotoRequest(photoBase, this.CONSTANT);
            if (res && res.code === 1) {
                this.handlePic = res.obj;
                await this.saveDriverPhoto();
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async saveDriverPhoto () {
            const res = await saveDriverPhoto({
                photo: cancelPrefix(this.handlePic),
                idPhoto: cancelPrefix(this.identifyCardPicture)
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async mailAddressUpload (mailInfo) {
            const res = await vehLicenseSaveMailInfo({
                idNum: this.identifyCard.certNumber,
                ...mailInfo
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async highPhotoNext (photo) {
            const res = await uploadPhysicalInfo({
                photo: cancelPrefix(photo)
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async driverPhotoHandle () {
            const result = await getIdentificationPhoto({ module: 'PhotoPrinter', directive: 'preview', obj: getDriverLicensePhoto(this.handlePic) });
            if (result && result.code === 1) {
                this.photoDogHandlePic = cancelPrefix(result.obj);
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: result.message, enableBack: true });
            }
        },
        async photoPrintNext () {
            const result = await printPhotoBase({ printCount: 1, base64: cancelPrefix(this.photoDogHandlePic) });
            if (result && result.code === 1) {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: result.message, enableBack: true });
            }
        },
        async tickPrint () {
            // this.completeInfo.paperPath

            // excelPrint({path:this.completeInfo.paperPath})

            const res = await excelPrint({
                path: request.getServerUrl() + this.paperPath
            });
            if (res && res.code === 1) {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: '打印机出故障啦，请稍后重试' });
            }

            // const data = {
            //     businessType: this.businessTypeName,
            //     lsh: this.lsh,
            //     dabh: this.driverLicenseInfo.dabh,
            //     certNumber: this.identifyCard.certNumber,
            //     applyTime: this.completeInfo.applyTime,
            //     partyName: this.identifyCard.partyName,
            //     blbm: this.driverLicenseInfo.blbm
            // };
            // const res = await tickPrint({ content: getDriverChangeTick(data) });
            // this.changeStep('down');
            // console.log('打印凭条结果：', res);
        },
        async getResultSerialNumber () {
            const res = await driverGetSerialNumber();
            if (res && res.code === '0') {
                this.completeInfo = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },
    mounted () {
        const message = mailTipMessage(this.CONSTANT.location);
        if (message) {
            this.mailTip = message;
        }

        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.driverExpiration) {
            this.setSteps(DRIVER_LICENSE_RENEWAL);
        } else {
            this.setSteps(DRIVER_LICENSE_LOST_RENEWAL);
        }
    }
};
</script>
